export default {
  USER_LOGIN(state, user) {
    state.user = user;
    state.loggedIn = true;
  },
  USER_LOGOUT(state) {
    state.loggedIn = false;
    state.user = undefined;
    state.profile = null;
  },
  USER_SIGNUP(state, user) {
    state.user = user;
  },
  SET_QR_CODE(state, qrCodeURL) {
    state.qrCode = qrCodeURL;
  },
  SET_QR_MODAL_OPEN(state, isOpen) {
    console.log("state qr code open:", state.qrModalOpen)
    state.qrModalOpen = isOpen;
  },
};